body {
  background-color: whitesmoke;
  font-family: "Roboto", "Lato", "Source Sans Pro", sans-serif;
}
.Navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
}
.TogglerContainer {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0px;
}
.SearchForm {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.SearchBox {
  width: 30%;
  margin-bottom: 5px;
}
.SearchButton {
  margin-bottom: 5px;
}
.NavLink {
  color: white;
}
.LoginForm {
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.LoginForm label {
  font-size: large;
  font-weight: bold;
}
.LoginForm input {
  margin-bottom: 3vh;
}
.LoginBtn {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}
.PasswordUl {
  list-style-type: none;
}
.PasswordUl li {
  margin-bottom: 10px;
  font-weight: bold;
}
.MainScreenSection {
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: larger;
}
.MainScreenImage {
  height: 300px;
  width: auto;
  margin-left: 2vw;
  border: 5px solid white;
  border-radius: 8px;
}
.SignUp {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 80px;
  -ms-flex-align: center;
      align-items: center;
}
.SignUp a {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-left: 5vw;
  font-size: larger;
  font-weight: bold;
}
.QtyBtnMin,
.QtyBtnPlus {
  color: grey;
}
.QtyBtnMin:focus,
.QtyBtnPlus:focus {
  outline: none !important;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.QtyBtnMin:active {
  color: red;
  -webkit-transition: ease-in-out;
  -o-transition: ease-in-out;
  transition: ease-in-out;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
}
.QtyBtnPlus:active {
  color: green;
  -webkit-transition: ease-in-out;
  -o-transition: ease-in-out;
  transition: ease-in-out;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
}
.QtyBtnMin:hover {
  color: red;
  -webkit-transition: ease-in-out;
  -o-transition: ease-in-out;
  transition: ease-in-out;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
}
.QtyBtnPlus:hover {
  color: green;
  -webkit-transition: ease-in-out;
  -o-transition: ease-in-out;
  transition: ease-in-out;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
}
Main {
  position: relative;
  height: 100%;
}
.Footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}



